import * as React from 'react';
import { BlockquoteWrapper, ShdUtil, theme } from '@ali/4ever-bamboo';
import { moFactory } from '@ali/4ever-plugin-blockquote';

export default function createBlockquotePlugin() {
  return {
    ...moFactory({}),
    renderNode(props, _, next) {
      const { node: { data } } = props;
      if (data && data.blockquote) {
        const { shd } = data;
        if (shd) {
          const shdStyle = {
            ...theme.DEFAULT_SHD_STYLES,
            backgroundColor: ShdUtil.transformShd(shd),
          };
          return (
            <div style={shdStyle}>
              <BlockquoteWrapper>{next()}</BlockquoteWrapper>
            </div>
          );
        } else {
          return <BlockquoteWrapper>{next()}</BlockquoteWrapper>;
        }
      }
      return next();
    },
  };
}
